body {
  font-family: $font-base;
  line-height: 1.6;
  margin: 0;
  padding: 0;
  background: #fff;
  color: $color-text;
}

.container {
  max-width: $breakpoint-lg;
  margin: 0 auto;
  padding: 1rem;
}

img { max-width: 100%; height: auto; }

h1, h2, h3 {
  line-height: 1.2;
  margin-top: 1.5rem;
}

.hero-header { line-height: 1.2; font-size: 2rem; }

table {
  width: 100%;
  border-collapse: collapse;
  overflow-x: auto;
  display: block;
}

th, td {
  padding: 0.5rem;
  border: 1px solid #ccc;
  text-align: left;
}

.categories {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin: 1rem 0;

  a {
    text-decoration: none;
    color: $color-primary;
    font-weight: bold;
  }
}

main {
  padding: 2rem;
}
